                e.preventDefault()//阻止默认的表单提交行为
                let formData=new FormData(f)

                // console.log(77,formData.get("file"),document.querySelector("input[type=file]").files)

                if(formData.get("file").name){
                    console.log('文件大小===>',formData.get("file").size)
                    if(formData.get("file").size<=5000){
                        Object.values(document.querySelector("input[type=file]").files)
                            .forEach(v=>{
                                this.ws.send("filename:"+v.name)
                                this.ws.send(v)//上传多文件 循环写入
                            })
                    }else {
                        alert('文件请小于5kb')
                    }

                }
                if(formData.get("msg")){
                    this.ws.send("msg:"+formData.get('msg'))
                    // this.ws.send(`{"Msg":}}`)
                }
                //发送完成重置表单
                f.reset()
            }

